import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '@/views/Home.vue'
import Services from '@/views/Services.vue'
import Contact from '@/views/Contact.vue'


import Carrelage from "@/components/Services/carrelage";
import parquet from "@/components/Services/parquet";
import faiences from "@/components/Services/faiences";

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/services',
    name: 'Services',
    component: Services
  },
  {
    path: '/contact',
    name: 'Contact',
    component: Contact
  },
  {
    path: '/service/faience',
    name: 'Faence',
    component: faiences
  },
  {
    path: '/service/parquet',
    name: 'Parquet',
    component: parquet
  },
  {
    path: '/service/carrelage',
    name: 'Carrelage',
    component: Carrelage
  }

]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
